:root {
    --c_primary: #29AB87;
    --c_secondary: #D0FDF1;
    --c_heading: #1A1A1A;
    --c_text: #626262;
    --c_text_dark: #1A1A1A;
    --c_grey: #EBEBEB;
    --c_grey_dark: #1A1A1A;
    --c_black: #000000;
    --c_white: #ffffff;
    --c_danger: #ff0000;
    --c_warning: #FFA500;
    --c_border1: #EAEAEA;
    --f_Urbanist: 'Urbanist', sans-serif;
    --shadow: 0px 2px 36px #00000014;
    --shadow_hover: 0px 2px 36px #00000030;
    --transition: all 0.3s ease-in-out 0s;
}

::-webkit-scrollbar {
    width: 5px;
    height: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--c_grey_dark);
}

::-webkit-scrollbar-thumb {
    transition: var(--transition);
    background: var(--c_primary);
    border-radius: 10px;
}

::-webkit-scrollbar-track {
    background-color: var(--c_secondary);
    box-shadow: inset 0 0 5px gray;
    border-radius: 10px;
}

* {
    margin: 0;
    outline: none !important;
    box-sizing: border-box;
    font-family: var(--f_Urbanist) !important;
}

html {
    scroll-behavior: smooth;
}

body {
    background-color: var(--c_white);
}

b,
strong {
    font-weight: 700;
}

a {
    color: var(--c_text);
    transition: var(--transition);
}

a:hover {
    color: var(--c_primary);
}

a,
a:not(.btn_n):hover {
    text-decoration: none;
}

:is(h1, h2, h3, h4, h5, h6) {
    color: var(--c_heading);
    font-weight: 700;
    line-height: 1.2;
    font-family: var(--f_Urbanist);
    margin-bottom: 0;
}

:is(h1, h2, h3, h4, h5, h6).fw_med {
    font-weight: 500;
}

:is(h1, h2, h3, h4, h5, h6):not(:last-child) {
    margin-bottom: 20px;
}

h1,
.hd_1 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 60px;
}

h2,
.hd_2 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 48px;
}

h3,
.hd_3 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 32px;
}

h4,
.hd_4 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 28px;
}

h5,
.hd_5 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 24px;
}

h6,
.hd_6 :where(h1, h2, h3, h4, h5, h6) {
    font-size: 20px;
}

:is(body, p, li) {
    font-size: 18px;
    font-weight: 400;
    line-height: 1.5;
    color: var(--c_text);
    font-family: var(--f_Urbanist);
}

:is(p, li) {
    margin: 0;
}

:is(p, li) small {
    font-size: 16px;
    display: block;
}

:is(p, ul, ol, li):not(:last-child) {
    margin-bottom: 15px;
}

ul {
    margin: 0;
    padding: 0;
    list-style: none;
}

img {
    max-width: 100%;
}

figure {
    margin: 0;
}

.ut_spc,
.u_spc {
    padding-top: 80px;
}

.ub_spc,
.u_spc {
    padding-bottom: 80px;
}

.uht_spc,
.uh_spc {
    padding-top: 60px;
}

.uhb_spc,
.uh_spc {
    padding-bottom: 60px;
}

.c_primary {
    color: var(--c_primary);
}

.bg_primary {
    background-color: var(--c_primary);
}

.c_secondary {
    color: var(--c_secondary);
}

.bg_secondary {
    background-color: var(--c_secondary);
}

.c_gradient {
    background: var(--c_gradient);
}

.bg_grey {
    background-color: var(--c_grey);
}

.bg_grey_light {
    background-color: var(--c_grey_light);
}

.bg_grey_dark {
    background-color: var(--c_grey_dark);
}

.c_white {
    color: var(--c_white);
}

.bg_white {
    background-color: var(--c_white);
}

.c_black {
    color: var(--c_black);
}

.bg_black {
    background-color: var(--c_black);
}

.c_danger {
    color: var(--c_danger);
}

.c_warning {
    color: var(--c_warning);
}

.text_center {
    text-align: center;
}

.d_block {
    display: block;
}

.btnn,
button.MuiButton-root {
    gap: 10px;
    font-size: 22px;
    font-weight: 700;
    min-height: 65px;
    padding: 5px 40px;
    border-radius: 40px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-transform: capitalize;
    box-shadow: none !important;
    transition: var(--transition);
    border: 2px solid transparent;
    letter-spacing: 0px;
    position: relative;
    text-indent: 0px;
    overflow: hidden;
    cursor: pointer;
    line-height: 1;
    z-index: 1;
}

.btnn.btn_xsm,
button.MuiButton-root.btn_xsm {
    font-size: 16px;
    min-height: 50px;
    padding: 5px 30px;
}

.btnn:hover,
button.MuiButton-root:hover {
    box-shadow: 0 0 20px 2px rgba(0, 0, 0, .1) !important;
}

.btnn::before,
button.MuiButton-root:before {
    content: "";
    animation: slide 2s infinite;
    background: linear-gradient(90deg, #40ebf100 0, #fffefd8f 50%, #fffefd47 69%, #40ebf100);
    bottom: 0;
    position: absolute;
    top: 0;
    width: 100%;
    z-index: -1;
}

@keyframes slide {
    0% {
        left: -100%;
    }

    100% {
        left: 100%;
    }
}

.btn_primary,
button.MuiButton-root.btn_primary {
    color: var(--c_white);
    background-color: var(--c_primary);
    border-color: var(--c_primary);
}

.btn_primary:is(:hover, :focus),
button.MuiButton-root.btn_primary:is(:hover, :focus) {
    color: var(--c_primary);
    background-color: transparent;
}

.btn_primary.br {
    color: var(--c_text_dark);
    background-color: var(--c_white);
    border-color: var(--c_primary);
}

.btn_primary.br:is(:hover, :focus) {
    color: var(--c_primary);
    background-color: var(--c_white);
    border-color: var(--c_primary);
}

.form .gap_p {
    --gap_x: 15px;
    --gap_y: 0px;
}

.control_group {
    margin-bottom: 20px;
    position: relative;
}

.control_group_flex {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

label {
    font-size: 14px;
    text-transform: capitalize;
    font-weight: 500;
    color: #272727;
    display: block;
    margin: 0 0 5px;
}

label.error {
    position: absolute;
    top: 100%;
    left: 0;
    color: #ff0000;
    font-weight: 500;
    font-size: 11px;
    letter-spacing: 0.2px;
    margin: 2px 0 0;
}

label.checkbox_label {
    font-size: 16px;
    color: var(--c_text);
    font-weight: 400;
    display: inline-flex;
    align-items: center;
    transition: var(--transition);
    cursor: pointer;
    margin: 0;
    gap: 8px;
}

.control_group .MuiInputBase-hiddenLabel .MuiOutlinedInput-notchedOutline {
    display: none;
}

input,
select,
textarea,
input.MuiInputBase-input,
input.MuiInputBase-input:-webkit-autofill,
.control_group .MuiSelect-select {
    width: 100%;
    font-size: 14px;
    font-weight: 400;
    color: #272727;
    border-radius: 40px;
    border: 1px solid #E2E2E2;
    background-color: var(--c_white);
    transition: var(--transition);
    outline: none !important;
    box-sizing: border-box;
    box-shadow: none;
}

input,
input.MuiInputBase-input {
    height: 50px;
    padding: 0 20px;
}

input:is([type="checkbox"], [type="radio"]) {
    width: 24px;
    height: 24px;
    min-width: 24px;
    appearance: none;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: var(--transition);
    padding: 0;
}

input:is([type="checkbox"], [type="radio"]) {
    color: var(--c_white);
    accent-color: var(--c_primary);
    cursor: pointer;
}

input:is([type="checkbox"], [type="radio"]):checked::before {
    transition: var(--transition);
    line-height: 1;
}

input[type="checkbox"],
.checkbox_label .MuiCheckbox-root .MuiSvgIcon-root {
    border-radius: 4px;
    border: 1px solid var(--c_grey);
    background-color: var(--c_grey);
}

input[type="checkbox"]:checked,
.checkbox_label .MuiCheckbox-root .MuiSvgIcon-root[data-testid="CheckBoxIcon"] {
    background-color: var(--c_primary);
    border-color: var(--c_primary);
}

input[type="checkbox"]:checked::before {
    content: "\2714";
    color: var(--c_white);
}

input[type="radio"] {
    border-radius: 50%;
    border: 1px solid #707070;
    background-color: var(--c_white);
}

input[type="radio"]:checked {
    border-color: var(--c_primary);
}

input[type="radio"]:checked::before {
    content: "\25CF";
    color: var(--c_primary);
    font-size: 28px;
    line-height: 1;
    position: relative;
    top: -2px;
}

.checkbox_label .MuiCheckbox-root,
.checkbox_label .MuiRadio-root {
    position: relative;
    padding: 0;
}

.checkbox_label .MuiCheckbox-root .MuiSvgIcon-root path {
    display: none;
}

.checkbox_label .MuiCheckbox-root.Mui-checked::after {
    content: "\2714";
    position: absolute;
    inset: 0;
    color: var(--c_white);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1;
}

.checkbox_label .MuiRadio-root.Mui-checked {
    color: var(--c_primary);
}

.checkbox_label svg[data-testid="RadioButtonCheckedIcon"] {
    fill: var(--c_primary);
}

select,
.control_group .MuiSelect-select {
    height: 50px !important;
    padding: 0 40px 0 20px !important;
    line-height: 50px;
    background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 16 16'%3e%3cpath fill='none' stroke='%23343a40' stroke-linecap='round' stroke-linejoin='round' stroke-width='2' d='M2 5l6 6 6-6'/%3e%3c/svg%3e");
    background-position: right 20px center;
    background-repeat: no-repeat;
    background-size: 13px 13px;
    appearance: none;
}

.control_group .MuiSelect-select[aria-expanded="true"] {
    border-color: var(--c_primary);
}

.control_group .MuiSelect-select~fieldset,
.control_group .MuiSelect-select~.MuiSvgIcon-root {
    display: none;
}

.control_group .MuiSelect-select:focus {
    border-radius: 40px;
}

.MuiList-root li {
    font-size: 14px;
    font-weight: 500;
    color: var(--c_text_dark);
    line-height: 1.3;
}

.MuiList-root li.Mui-selected,
.MuiList-root li.Mui-selected:hover {
    color: var(--c_white);
    background-color: var(--c_primary);
}

.MuiList-root li:not(:last-child) {
    margin-bottom: 0;
}

textarea {
    height: 100px;
    padding: 10px 20px;
    border-radius: 10px;
    resize: none;
}

:is(input, select, textarea, input.MuiInputBase-input)::placeholder {
    color: var(--c_text);
    font-weight: 400;
    opacity: 1;
}

:is(input, select, textarea, input.MuiInputBase-input):focus {
    border-color: var(--c_primary);
}

:is(input, select, textarea, .control-group).empty_value {
    border-color: #ff0000;
}

.form_btn {
    margin-top: 25px;
}

.form_btn .btnn {
    font-size: 18px;
}

.control_group .MuiFormControl-root.MuiTextField-root>.MuiInputBase-root {
    padding: 0;
}

.control_group .eye_btn {
    position: absolute;
    height: 50px;
    max-height: 50px;
    width: 50px;
    margin: 0;
    right: 0;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
}

.control_group .eye_btn button {
    height: 80%;
    width: 80%;
    display: flex;
    align-items: center;
    justify-content: center;
    padding: 0;
    margin: 0 auto;
}

.control_group .eye_btn button svg {
    font-size: 20px;
}

:is(.gap_p, .gap_m) {
    --gap_x: 30px;
    --gap_y: 30px;
    display: flex;
    flex-wrap: wrap;
    margin-top: calc(var(--gap_y) * -1);
    margin-left: calc(var(--gap_x) * -1);
}

:is(.gap_p, .gap_m).aic {
    align-items: center;
}

:is(.gap_p, .gap_m).jcsb {
    justify-content: space-between;
}

:is(.gap_p, .gap_m)>* {
    flex-shrink: 0;
    width: 100%;
    max-width: 100%;
    margin-top: var(--gap_y);
}

.gap_p>* {
    padding-left: var(--gap_x);
}

.gap_m>* {
    margin-left: var(--gap_x);
}

.w_50 {
    width: 50%;
}

.w_100 {
    width: 100%;
}

.conta_iner {
    width: 90%;
    max-width: 1170px;
    margin-inline: auto;
}

@media (max-width: 767.98px) {
    .conta_iner {
        width: 100%;
        padding: 0 15px;
    }
}

.s_head {
    margin-bottom: 50px;
}

.s_head :is(.left_s, .right_s) {
    width: 50%;
}

.content {
    min-height: calc(100vh - (91px + 357px));
}

.typed-cursor {
    font-weight: 300 !important;
    color: var(--c_text) !important;
    position: relative;
    padding-left: 2px;
    top: -3px;
}

.btn_flex {
    display: flex;
    align-items: center;
    margin-top: 30px;
    gap: 20px;
}

.iti--allow-dropdown {
    width: 100%;
}

.iti--separate-dial-code .iti__selected-flag {
    background-color: transparent;
}

.iti--allow-dropdown .iti__flag {
    transform: scale(0.8);
}

.iti--separate-dial-code .iti__selected-dial-code {
    margin-left: 4px;
    font-size: 14px;
    color: #272727;
}

.iti--allow-dropdown .iti__arrow {
    margin-left: 5px;
    border-left: 4px solid transparent;
    border-right: 4px solid transparent;
    border-top: 5px solid #272727;
}

.iti--allow-dropdown .iti__arrow--up {
    border-top: none;
    border-bottom: 5px solid #272727;
}

.iti--allow-dropdown .iti__selected-flag {
    padding: 0 10px 0 15px;
    background-color: transparent !important;
}

.iti__country-list .iti__country {
    margin: 0;
    font-size: 14px;
    color: #272727;
}

.custom_tabs {
    display: flex;
}

.custom_tabs .MuiTabs-flexContainer {
    gap: 10px;
}

.custom_tabs .MuiTabs-scroller {
    display: inline-flex;
}

.custom_tabs .MuiTabs-indicator {
    display: none;
}

.custom_tabs .nav-link {
    min-width: 142px;
    min-height: 44px;
    font-size: 20px;
    font-weight: 400;
    letter-spacing: 0;
    padding: 5px 12px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    color: var(--c_text_dark);
    text-transform: capitalize;
    font-family: var(--f_Urbanist);
    background-color: var(--c_grey);
    border-radius: 15px 15px 0px 0px;
    transition: var(--transition);
}

.custom_tabs .nav-link:hover {
    color: var(--c_primary);
}

.custom_tabs .nav-link.Mui-selected {
    color: var(--c_white);
    background-color: var(--c_primary);
}

.text_tabs {
    display: flex;
    justify-content: center;
    gap: 10px;
}

.text_tabs .nav-link {
    padding: 0;
    min-width: 1px;
    min-height: 1px;
    font-size: 16px;
    border-radius: 0;
    font-weight: 400;
    border: none;
    color: var(--c_text_dark);
    display: inline-flex;
    align-items: center;
    justify-content: center;
    text-decoration: underline;
    background-color: transparent;
    transition: var(--transition);
    cursor: pointer;
}

.text_tabs .nav-link:hover {
    color: var(--c_primary);
}

.text_tabs .nav-link.active {
    color: var(--c_text_dark);
    background-color: transparent;
    display: none;
}

.site_breadcrum {
    padding: 50px 0;
}

.site_breadcrum ul {
    display: flex;
    flex-wrap: wrap;
}

.site_breadcrum li {
    margin: 0;
    font-size: 16px;
    font-weight: 500;
    display: inline-flex;
    align-items: center;
    color: var(--c_grey_dark);
}

.site_breadcrum li:not(:last-child)::after {
    content: "";
    width: 18px;
    height: 9px;
    background-image: url("../public/static/images/longArrow_icon.svg");
    background-repeat: no-repeat;
    background-size: contain;
    margin: 0 10px;
}

.site_breadcrum li a {
    cursor: pointer;
}

.table {
    width: 100%;
    border-collapse: collapse;
}

.table-responsive {
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
}

/* Header */
.site_header {
    position: sticky;
    left: 0;
    top: 0;
    z-index: 9;
    background-color: white;
    transition: var(--transition);
    /* padding: 30px 0; */
    padding: 15px 0;
    width: 100%;
}

.site_header.scrolled,
body:not(:has(.home_page)) .site_header {
    background-color: var(--c_white);
    box-shadow: var(--shadow);
}

.site_header nav {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.site_hamburgur {
    display: none;
}

.site_logo {
    font-size: 40px;
    font-weight: 700;
    color: var(--c_primary);
    display: inline-flex;
    max-width: 167px;
    cursor: pointer;
    line-height: 1;
}

.site_logo:hover {
    color: var(--c_primary);
}

.site_logo figure {
    display: inline-flex;
    width: 100%;
}

.site_logo figure img {
    width: 100%;
}

.site_menu {
    display: inline-flex;
    align-items: center;
    gap: 0 40px;
}

.site_menu>li {
    margin: 0;
    position: relative;
    display: inline-flex;
}

.site_menu>li>a:not(.btnn, .user_btn, .flag_btn) {
    color: var(--c_text_dark);
    font-size: 20px;
    font-weight: 400;
    padding: 5px 0;
    cursor: pointer;
}

.site_menu>li>a:not(.btnn, .user_btn):hover {
    color: var(--c_primary);
}

.site_menu>li>a.btnn {
    min-height: 40px;
    font-size: 18px;
    padding: 5px 20px;
    min-width: 125px;
    text-transform: uppercase;
}

.flag_btn {
    color: var(--c_black);
    display: inline-flex;
    align-items: center;
    cursor: pointer;
    gap: 3px;
}

.flag_btn .MuiButtonBase-root {
    padding: 0;
}

.flag_btn figure {
    width: 40px;
    min-width: 40px;
    height: 40px;
    border-radius: 50%;
    overflow: hidden;
}

.flag_btn figure img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.flag_btn svg {
    font-size: 22px;
    margin-right: -5px;
}

.menu_drop .MuiPaper-root {
    min-width: 125px;
    border-radius: 10px;
    box-shadow: var(--shadow);
    background-color: var(--c_white);
    margin-top: 5px;
}

.menu_drop .MuiPaper-root ul {
    padding: 5px 0;
}

.menu_drop .MuiPaper-root ul li {
    margin: 0;
    padding: 0;
    display: flex;
    background-color: transparent;
}

.menu_drop .MuiPaper-root ul li a {
    display: flex;
    font-size: 16px;
    font-weight: 500;
    color: var(--c_text_dark);
    font-family: var(--f_Urbanist);
    padding: 3px 10px;
    width: 100%;
}

.menu_drop .MuiPaper-root ul li a:hover {
    font-weight: 700;
    color: var(--c_primary);
}

.menu_drop .MuiPaper-root ul li a figure {
    display: flex;
    align-items: center;
    white-space: nowrap;
    gap: 5px;
}

.menu_drop .MuiPaper-root ul li a figure img {
    width: 30px;
    min-width: 30px;
    height: 30px;
    border-radius: 50%;
    object-fit: cover;
}

.user_btn {
    height: 40px;
    padding: 4px 10px 4px 4px;
    background-color: var(--c_white);
    border-radius: 40px;
    display: inline-flex;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    font-weight: 500;
    color: var(--c_text_dark);
    box-shadow: var(--shadow);
    transition: var(--transition);
    cursor: pointer;
    gap: 6px;
}

.user_btn:hover {
    box-shadow: var(--shadow_hover);
    color: var(--c_primary);
}

.site_menu .dropdown-toggle::after {
    display: none;
}

.user_btn span {
    display: inline-block;
    max-width: 70px;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
}

.user_btn svg {
    font-size: 18px;
    position: relative;
    margin: 0 -4px 0 -5px;
    top: 1px;
}

.user_btn>.MuiButtonBase-root {
    width: 33px;
    height: 33px;
    border-radius: 50%;
    overflow: hidden;
    padding: 0;
}

.user_btn>.MuiButtonBase-root>div {
    width: 100%;
    height: 100%;
}

/* Footer */
.footer_nav {
    display: flex;
    justify-content: space-between;
    gap: 20px;
}

.footer_nav .single {
